        autoplay:false,//
        preload:'none',//: 'none', 'metadata', 'auto'
        volume:0.5,//
        playbackSpeed:[0.5, 0.75, 1, 1.25, 1.5, 2], //
        video: {
            url: "https://api.dogecloud.com/player/get.mp4?vcode=5ac682e6f8231991&userId=17&ext=.mp4",
            pic:'',
        },
     });
     console.log(dp);
     //dp.seek(time: number): 
     //dp.pause(): 
     //dp.play(): 
 }
};
</script>

